@import 'vars.scss';
@import 'helpers.scss';
@import 'base.scss';
@import 'calendar.scss';
@import 'todo.scss';

html {
    font-size: 18px;
    font-weight: 400;
    font-family: $font-family;
    color: #333;
}

body {
    background-image: linear-gradient(to bottom, #334, #446);
    background-size: cover;
    background-attachment: fixed;
    margin: 0;
}

[v-cloak] {
    display: none !important;
}

.wrap {
    margin: 0 auto;
    max-width: 1200px;
    text-align: center; 
}

.app {
    box-shadow: 0 .8rem 2rem rgba(0,0,0,0.5);
    border-radius: 4px;
    overflow: hidden;
    margin: 1rem;
    height: calc(100vh - 2rem);
    min-height: 400px; 
    @media screen and (max-width: 768px) {
        margin: 0;
        height: 100vh;
    }
}

.toolbar {
    background: #eee;
    height: 3rem;
    position: relative;
    font-size: .9rem;
}

h1 {
    font-size: .9rem;
    line-height: 3rem;
    font-weight: 600;
    margin: 0;
    display: inline;
}

.full-button {
    @include button;

    background: transparent;
    position: absolute;
    right: .5rem;
    top: .5rem;
    color: #888;
    padding: 0 .5rem;
    height: 2rem;
    border-radius: $border-radius;
    transition: 150ms ease;

    &:hover {
        background: rgba(#000, 0.1);
    }

    &:active {
        background: rgba(#000, 0.2);
        box-shadow: inset 0 2px 10px rgba(#000,0.25);
    }
}

.container {
    display: flex;
    background: #fff;
    overflow: hidden;
    height: calc(100% - 3rem);
}





td.selected {
    box-shadow: inset 0 0 0 2px #28f;
}